import React, {
  createContext,
  useContext,
  ReactNode,
  useMemo,
  useEffect,
} from 'react'
import { useToast } from 'hooks/useToast'
import * as T from 'providers/ToastProvider.style'
import { ToastDuration, ToastStatus, ToastText } from 'types/toast'

interface ToastContextProps {
  addToast: (
    text: ToastText,
    type: ToastStatus,
    duration?: ToastDuration,
  ) => void
}

//   
let addToastGlobal: (
  text: ToastText,
  type: ToastStatus,
  duration?: ToastDuration,
) => void

export const ToastContext = createContext<ToastContextProps | undefined>(
  undefined,
)

//   
export const useToastContext = () => {
  const context = useContext(ToastContext)
  if (!context) {
    throw new Error('    .')
  }
  return context
}

export function ToastProvider({ children }: { children: ReactNode }) {
  const { toasts, addToast } = useToast()

  //     
  useEffect(() => {
    if (!addToastGlobal) {
      addToastGlobal = addToast
    }
  }, [addToast])

  // value  useMemo      
  const value = useMemo(() => ({ addToast }), [addToast])

  return (
    <ToastContext.Provider value={value}>
      {children}
      <T.ToastContainer>
        {toasts.map(toast => (
          <T.ToastProviderStyle key={toast.id} $type={toast.type}>
            {toast.text}
          </T.ToastProviderStyle>
        ))}
      </T.ToastContainer>
    </ToastContext.Provider>
  )
}

//      addToast 
export const addToastMessage = (
  message: string,
  type: ToastStatus = 'default',
  duration = 3000,
) => {
  if (addToastGlobal) {
    addToastGlobal(message, type, duration)
  }
}
